import React from 'react'
import { useEffect } from "react";
import { PostAPI } from "../../../../services/Service";
import "bootstrap-icons/font/bootstrap-icons.css";
import  { useState } from "react";
import { PAGINATION_PER_PAGE } from "../../../../utils/Constants";
import Loader from "../../../assest/UI/Loader";
import { toast } from "react-toastify";
import Network from "./Network";
import "./Chatstyle.css";
import { useNavigate } from "react-router-dom";
import Pagination from "../../Pagination";

const userDefaultData = {
    // user_id: "",
    activity_id: "",
    activity_name: "",
    activity_sub_name: "",
    activity_end_date: "",
    //activity_date: "",
    status: "upcomming",
  };
  

const UpcomingActivity = () => {
    let navigate = useNavigate();
    const [items_perpage, setitems_perpage] = useState(PAGINATION_PER_PAGE);
    const [current_page, setCurrent_page] = useState(1);
    const [totalPages, setTotalPage] = useState(0);
    const [isLoading, setIsLoading] = useState(false);
    const [search, setSearch] = useState("");
    const [hideList, setHindeList] = useState(false);
    const [categoryData, setCategoryData] = useState(userDefaultData);
    const [activeTab, setActiveTab] = useState("first");
    const [completeList, setCompleteList] = useState([]);
    const [categoryList, setcategoryList] = useState([]);
    const [upcommingList, setUpcommingList] = useState([]);
    const [condition, setCondition] = useState(false);

    const getCategoryList = async () => {
        setIsLoading(true);
        const useListData = await PostAPI("category/category_list", {
          items_perpage: items_perpage,
          current_page: current_page,
          search: search,
        });
    
        if (useListData.response_code == 200) {
          setcategoryList(useListData.data.result);
          setTotalPage(useListData.data.total);
          setIsLoading(false);
        } else {
          toast.error(useListData.message);
          setIsLoading(false);
        }
      };
    
      //Search handle
      const searchHandler = (e) => {
        e.preventDefault();
        setHindeList(true);
        setSearch(e.target.value);
      };
    
      useEffect(() => {
        getCategoryList();
      }, [search]);

      useEffect(() => {
        getCategoryList();
      }, [current_page]);

      const pagechangeHandler = (offset) => {
        //debugger;
        //alert(offset)
        setCurrent_page(offset);
      };

    
      const handleClickInputValue = (result, id) => {
        setSearch(result);
        setHindeList(false);
        setCategoryData({
          ...categoryData,
          ["activity_name"]: result,
          ["activity_id"]: id,
        });
        //userDefaultData.activity_id=id;
      };
    
      //tab
      const handleTabSelect = (tab) => {
        setActiveTab(tab);
      };
    
      //Add data
      const handleChange = (e) => {
        const { name, value } = e.target;
        setCategoryData({
          ...categoryData,
          [name]: value,
        });
      };
    
      const submitHandler = async (e) => {
        e.preventDefault(); //
        setIsLoading(true);
    
        const RegisterCategory = await PostAPI(
          "user_activity/add_user_activity",
          categoryData
        );
    
        if (RegisterCategory.response_code == "200") {
          toast(RegisterCategory.message);
          setIsLoading(false);
        }
    
        if (RegisterCategory.response_code == "500") {
          toast.error(RegisterCategory.message);
          setIsLoading(false);
        }
        // getCompleteData();
        getUpcommingData();
        setCondition(true);
      };
    
      useEffect(() => {
        // getCompleteData();
        getUpcommingData();
      }, condition);
    
      
    
      useEffect(() => {
        // getCompleteData();
      }, categoryData);
    
      useEffect(() => {
        // getCompleteData();
      }, []);
    
      //get upcomming data
      const getUpcommingData = async () => {
        debugger;
        const useUpcommingData = await PostAPI("user_activity/upcomming_data");
        if (useUpcommingData.response_code == 200) {
          setUpcommingList(useUpcommingData.data);
        }
      };
    
      useEffect(() => {
        getUpcommingData();
      }, categoryData);
    
      useEffect(() => {
        getUpcommingData();
      }, []);
    
  return (

    <div className='content-wrapper'>
      <div className='row'>
      <div class="page-header">
            <h3 class="page-title">Upcoming Activity </h3>
          </div>
      <div class="col-lg-12 grid-margin stretch-card">
            <div class="card">
              <div class="card-body">
     
             
                {/* <ul class="nav nav-fill nav-tabs" role="tablist">
                  <li class="nav-item" role="presentation">
                    <a
                      class="nav-link active"
                      id="fill-tab-0"
                      data-bs-toggle="tab"
                      href="#fill-tabpanel-0"
                      role="tab"
                      aria-controls="fill-tabpanel-0"
                      aria-selected="true"
                    >
                      {" "}
                      Upcoming Activity{" "}
                    </a>
                  </li>
                  
                </ul> */}

                <div class="tab-content pt-5" id="tab-content">
                  <div
                    class="tab-pane active"
                    id="fill-tabpanel-0"
                    role="tabpanel"
                    aria-labelledby="fill-tab-0"
                  >
                    <table class="table table-striped">
                      <thead>
                        <tr>
                          <th> Activity </th>
                          <th> Sub_Activity </th>
                          <th> End Date</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          {" "}
                          {upcommingList == 0 ? <p>No record found</p> : ""}
                        </tr>
                        {upcommingList.map((value) => {
                          return (
                            <tr>
                              <td>
                                {" "}
                                <i class="bi bi-check-circle-fill"></i>I have{" "}
                                {value.activity_name}
                              </td>
                              <td> {value.activity_sub_name}</td>
                              <div>
                              <td>{value.activity_end_date}</td>
                              </div>
                            </tr>
                          );
                        })}
                      </tbody>
                    </table>
                  </div>

                  <div
                    class="tab-pane"
                    id="fill-tabpanel-1"
                    role="tabpanel"
                    aria-labelledby="fill-tab-1"
                  >
                    <table class="table table-striped">
                      <thead>
                        <tr>
                          <th>Activity </th>
                          <th> Sub_Activity </th>
                          <th> End Date </th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          {" "}
                          {completeList == 0 ? <p>No record found</p> : ""}
                        </tr>
                        {completeList.map((value) => {
                          return (
                            <tr>
                              <td>
                                {" "}
                                <i class="bi bi-check-circle-fill"></i>I have{" "}
                                {value.activity_name}
                              </td>
                              <td> {value.activity_sub_name}</td>
                              <td>{value.activity_end_date}</td>
                            </tr>
                          );
                        })}
                      </tbody>
                    </table>
                    {isLoading && <Loader />}
                {totalPages > 0 && (
                  <Pagination
                    itemsPerPage={items_perpage}
                    totalPage={totalPages}
                    pagechangeHandler={pagechangeHandler}
                  />
                )}
                  </div>
                </div>
              </div>
           
    </div>
    </div>
          </div>
    </div>
  )
}

export default UpcomingActivity