import React from "react";
import avtar from "./avtar.jpeg";
import Chat from "./Chat";
import { PostAPI } from "../../../../services/Service";
import { useState, useEffect } from "react";
import {
  useNavigate
} from "react-router-dom";

function Network() {
  const [receiverList, setReceiverList] = useState([]);
  let navigate = useNavigate();

  //Reciver List
  const getRecieverUserList = async () => {
    const useListData = await PostAPI("chat/chat_users_list");

    if (useListData.response_code == "200") {
      setReceiverList(useListData.data);
    } else {
      //error(useListData.message);
    }
  };

  useEffect(() => {
    getRecieverUserList();
  }, []);

    return (
      <div class="content-wrapper">
        <div className="row">

      <main role="main" class="col-md-11 pt-3 px-4">

        <div class="chat">
        {receiverList.map((value) => {
                  return (
          <div class="chat-header clearfix">
            <img
              src="https://s3-us-west-2.amazonaws.com/s.cdpn.io/195612/chat_avatar_01_green.jpg"
              alt="avatar"
            />

            <div class="chat-about">
              <div class="chat-with">Jitendra Patel</div>
              <div class="chat-num-messages">Hello Everyone</div>
              
              </div>
              <button className="float-right btn btn-gradient-primary me-2">Request</button>
            {/* <i class="fa fa-star"></i> */}
          </div>
          );
        })}
          
        </div>
      
    </main>
    </div>
    </div>
  );
}
export default Network;
