import { useEffect } from "react";
import { PostAPI } from "../../../../services/Service";
import "bootstrap-icons/font/bootstrap-icons.css";
import React, { useState } from "react";
import { PAGINATION_PER_PAGE } from "../../../../utils/Constants";
import { toast } from "react-toastify";
import "./Chatstyle.css";
import { useNavigate } from "react-router-dom";

const userDefaultData = {
  // user_id: "",
  activity_id: "",
  activity_name: "",
  activity_sub_name: "",
  activity_end_date: "",
  //activity_date: "",
  status: "upcomming",
};

function ListActivityData() {
  let navigate = useNavigate();
  const [items_perpage, setitems_perpage] = useState(PAGINATION_PER_PAGE);
  const [current_page, setCurrent_page] = useState(1);
  const [totalPages, setTotalPage] = useState(0);
  const [isLoading, setIsLoading] = useState(false);
  const [search, setSearch] = useState("");
  const [hideList, setHindeList] = useState(false);
  const [categoryData, setCategoryData] = useState(userDefaultData);
  const [activeTab, setActiveTab] = useState("first");
  const [completeList, setCompleteList] = useState([]);
  const [categoryList, setcategoryList] = useState([]);
  const [upcommingList, setUpcommingList] = useState([]);
  const [condition, setCondition] = useState(false);
  //get data
  const getCategoryList = async () => {
    setIsLoading(true);
    const useListData = await PostAPI("category/category_list", {
      items_perpage: items_perpage,
      current_page: current_page,
      search: search,
    });

    if (useListData.response_code == 200) {
      setcategoryList(useListData.data.result);
      setTotalPage(useListData.data.total);
      setIsLoading(false);
    } else {
      toast.error(useListData.message);
      setIsLoading(false);
    }
  };

  //Search handle
  const searchHandler = (e) => {
    e.preventDefault();
    setHindeList(true);
    setSearch(e.target.value);
  };

  useEffect(() => {
    getCategoryList();
  }, [search]);

  const handleClickInputValue = (result, id) => {
    setSearch(result);
    setHindeList(false);
    setCategoryData({
      ...categoryData,
      ["activity_name"]: result,
      ["activity_id"]: id,
    });
    //userDefaultData.activity_id=id;
  };

  //tab
  const handleTabSelect = (tab) => {
    setActiveTab(tab);
  };

  //Add data
  const handleChange = (e) => {
    const { name, value } = e.target;
    setCategoryData({
      ...categoryData,
      [name]: value,
    });
  };

  const submitHandler = async (e) => {
    e.preventDefault(); //
    setIsLoading(true);

    const RegisterCategory = await PostAPI(
      "user_activity/add_user_activity",
      categoryData
    );

    if (RegisterCategory.response_code == "200") {
      toast(RegisterCategory.message);
      setIsLoading(false);
    }

    if (RegisterCategory.response_code == "500") {
      toast.error(RegisterCategory.message);
      setIsLoading(false);
    }
    getCompleteData();
    // getUpcommingData();
    setCondition(true);
  };

  useEffect(() => {
    getCompleteData();
    // getUpcommingData();
  }, condition);

  //get complete data
  const getCompleteData = async () => {
    debugger;
    const useCompleteData = await PostAPI("user_activity/complete_data");

    if (useCompleteData.response_code == 200) {
      setCompleteList(useCompleteData.data);
    }
  };

  useEffect(() => {
    getCompleteData();
  }, categoryData);

  useEffect(() => {
    getCompleteData();
  }, []);

  useEffect(() => {
    // getUpcommingData();
  }, categoryData);

  useEffect(() => {
    // getUpcommingData();
  }, []);

  return (
    <div>
      <div className="content-wrapper">
        <div class="row">
        <div class="page-header">
            <h3 class="page-title">Complete Activity </h3>
          </div>
          <div class="col-lg-12 grid-margin stretch-card">
            <div class="card">
              <div class="card-body">
          
                {/* <ul class="nav nav-fill nav-tabs" role="tablist">
                  <li class="nav-item" role="presentation">
                    <a
                      class="nav-link active "
                      id="fill-tab-1"
                      data-bs-toggle="tab"
                      href="#fill-tabpanel-1"
                      role="tab"
                      aria-controls="fill-tabpanel-1"
                      aria-selected="false"
                    >
                      {" "}
                      Completed Activity{" "}
                    </a>
                  </li>
                </ul> */}

                <div
                  class="tab-pane"
                  id="fill-tabpanel-1"
                  role="tabpanel"
                  aria-labelledby="fill-tab-1"
                >
                  <table class="table table-striped">
                    <thead>
                      <tr>
                        <th>Activity </th>
                        <th> Sub_Activity </th>
                        <th> End Date </th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        {" "}
                        {completeList == 0 ? <p>No record found</p> : ""}
                      </tr>
                      {completeList.map((value) => {
                        return (
                          <tr>
                            <td className="p-3">
                              {" "}
                              <i class="bi bi-check-circle-fill"></i>I have{" "}
                              {value.activity_name}
                            </td>
                            <td className="p-3"> {value.activity_sub_name}</td>
                            
                            
                            <td className="p-3">{value.activity_end_date}</td>
                          
                          </tr>
                        );
                      })}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ListActivityData;
